
@import 'variables';
//@import 'mixins';

body {
    background: #fff;
    font-family: Arial;
    font-size: 13px;
    color: #3f484f;
}


.pagebreak {
    page-break-before: always;
}

table {
    font-size: 13px;
}

    table tr {
        font-size: 13px;
    }

        table tr th {
            padding: 2px 0;
            text-align: left;
            font-weight: bold;
        }

            table tr th.padLeft {
                padding-left: 10px;
            }

            table tr th.padRight {
                padding-right: 10px;
            }

            table tr th.align-right {
                text-align: right;
            }

            table tr th.align-left {
                text-align: left;
            }

            table tr th.align-center {
                text-align: center;
            }

        table tr td {
            padding: 2px 0;
            line-height: 18px;
        }

            table tr td.padLeft {
                padding-left: 10px;
            }

            table tr td.padRight {
                padding-right: 10px;
            }

            table tr td.small {
                padding: 0;
                line-height: 10px;
                font-size: 10px;
            }

            table tr td.align-right {
                text-align: right;
            }

            table tr td.align-left {
                text-align: left;
            }

            table tr td.align-center {
                text-align: center;
            }

            table tr td.valign-top {
                vertical-align: top;
            }

            table tr td.valign-middle {
                vertical-align: middle;
            }

            table tr td.line {
                vertical-align: bottom;
            }

        table tr td img.product {
            max-width: 100px;
            width: 100px;
        }

        table tr.total td.padRight {
            padding-right: 10px;
        }

h3 {
    font-size: 18px;
    margin: 0;
    padding: 0;
}

a {
    color: #606060;
}
